*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

span{
    display: inline-block;
    text-decoration: none;
    color: inherit;
}

.sectoin-head h4{
    position: relative;
    padding: 0;
    color: red;
    line-height: 1;
    letter-spacing: 0.3px;
    font-weight: 700;
    text-align: center;
    text-transform: none;
    margin-bottom: 36px;
}

.sectoin-head h4::before{
    content: "";
    width: 64px;
    height: 3px;
    background: red;
    position: absolute;
    bottom: 30px;
    left: 0;
    right: 0;
    margin: 0 auto;
}


.sectoin-head h4 span{
    font-weight: 700px;
    padding-bottom: 5px;
    color: green;
}

.sectoin-head p{
    color: blue;
    font-size: 16px;
    line-height: 28px;
    text-align: center;
}

.item{
    background: #fff;
    text-align: center;
    padding: 20px 25px;
    box-shadow: 0 0 25px rgba(0,0,0,0,0.7);
    border-radius: 20px;
    margin-bottom: 30px;
    border: 5px solid rgba(0,0,0,0,0.7);
    transition: all 0.5s ease 0s;
}

.item:hover{
    background: red;
    box-shadow: 0 8px 20px 0 rgba(0,0,0,0,0.2);
    transition:  all 0.5 ease 0s;
}

.item:hover .item, .item:hover span.icon{
    background: white;
    border-radius: 10px;
    transition: all 0.5s ease 0s;
}

.item:hover h6, .item:hover p{
    color: white;
    transition: all 0.5s ease 0.5s;
}

.item .icon{
    font-size: 40px;
    margin-bottom: 25px;
    color: red;
    width: 90px;
    height: 90px;
    line-height: 96px;
    border-radius: 50px;
}

.item .feature_box_col_one{
    background: rgba(247, 195, 5, 0.2);
    color: red;
}

.item .feature_box_col_two{
    background:rgba(255,77,28,015);
    color: red;
}

.item .feature_box_col_three{
    background: rgba(0,147,38,0.15);
    color: red;
}

.item .feature_box_col_four{
    background: rgba(0,108,255,0.15);
    color: red;
}

.item .feature_box_col_five{
    background: rgba(146,39,255);
    color: red;
}

.item .feature_box_col_six{
    background: rgba(23,39,246,0.15);
    color: red;
}

.item .feature_box_col_seven{
    background: rgba(23, 39, 246, 0.15);
    color: red;
}


